import { VideoSource } from './types';

// Multiple video embed APIs configuration - scalable to many providers
const VIDEO_APIS = [
  {
    name: 'VidSrc',
    baseUrl: process.env.NEXT_PUBLIC_VIDEO_EMBED_1 || 'https://vidsrc.to/embed/movie/${id}',
    priority: 1,
    supportsTv: true,
    supportsMovies: true
  },
  {
    name: 'VidSrc Pro',
    baseUrl: process.env.NEXT_PUBLIC_VIDEO_EMBED_2 || '',
    priority: 2,
    supportsTv: true,
    supportsMovies: true
  },
  {
    name: 'Embed Movies',
    baseUrl: process.env.NEXT_PUBLIC_VIDEO_EMBED_3 || '',
    priority: 3,
    supportsTv: true,
    supportsMovies: true
  },
  {
    name: 'SuperEmbed',
    baseUrl: process.env.NEXT_PUBLIC_VIDEO_EMBED_4 || '',
    priority: 4,
    supportsTv: true,
    supportsMovies: true
  }
];

// Filter and sort APIs by priority
const getConfiguredApis = (mediaType?: 'movie' | 'tv') => {
  return VIDEO_APIS
    .filter(api => {
      if (!api.baseUrl) return false;
      if (mediaType === 'movie') return api.supportsMovies;
      if (mediaType === 'tv') return api.supportsTv;
      return true;
    })
    .sort((a, b) => a.priority - b.priority);
};

// Create video source with enhanced metadata
function createVideoSource(url: string, api: typeof VIDEO_APIS[0], mediaType: 'movie' | 'tv', tmdbId: number): VideoSource {
  return {
    url: url,
    quality: 'HD',
    type: 'iframe',
    servers: api.name,
    apiIndex: VIDEO_APIS.indexOf(api),
    mediaType,
    tmdbId,
    isWorking: null // Will be updated after testing
  };
}

// URL processing function - handles dynamic URL construction
function processVideoUrl(api: typeof VIDEO_APIS[0], mediaType: 'movie' | 'tv', tmdbId: number, season?: number, episode?: number): string {
  const baseUrl = api.baseUrl.replace(/\/+$/, ''); // Remove trailing slashes
  
  switch (mediaType) {
    case 'movie':
      // Multiple URL patterns supported
      if (api.name === 'VidSrc') {
        return `${baseUrl}/movie/${tmdbId}`;
      } else if (api.name === 'VidSrc Pro') {
        return `${baseUrl}/movie?tmdb=${tmdbId}`;
      } else if (api.name === 'Embed Movies') {
        return `${baseUrl}/embed/movie/${tmdbId}`;
      } else {
        return `${baseUrl}/movie/${tmdbId}`;
      }
      
    case 'tv':
      if (!season || !episode) return '';
      
      if (api.name === 'VidSrc') {
        return `${baseUrl}/tv/${tmdbId}/${season}/${episode}`;
      } else if (api.name === 'VidSrc Pro') {
        return `${baseUrl}/tv?tmdb=${tmdbId}&season=${season}&episode=${episode}`;
      } else if (api.name === 'Embed Movies') {
        return `${baseUrl}/embed/tv/${tmdbId}/${season}/${episode}`;
      } else {
        return `${baseUrl}/tv/${tmdbId}/${season}/${episode}`;
      }
      
    default:
      return '';
  }
}

// Enhanced API testing function
async function testVideoSource(source: VideoSource): Promise<boolean> {
  try {
    const controller = new AbortController();
    const timeoutId = setTimeout(() => controller.abort(), 5000);
    
    const response = await fetch(source.url, {
      method: 'HEAD',
      mode: 'no-cors',
      signal: controller.signal
    });
    
    clearTimeout(timeoutId);
    return true;
  } catch (error) {
    console.warn(`Source ${source.servers} failed test:`, error);
    return false;
  }
}

// Main video API service
export const videoApi = {
  // Get movie sources with fallback priority
  async getMovieSources(tmdbId: number): Promise<VideoSource[]> {
    const configuredApis = getConfiguredApis('movie');
    
    if (configuredApis.length === 0) {
      console.warn('No movie APIs configured');
      return [];
    }

    // Create all possible sources
    const sources = configuredApis.map(api => 
      createVideoSource(processVideoUrl(api, 'movie', tmdbId), api, 'movie', tmdbId)
    );

    // Test first source and return if working
    for (const source of sources) {
      const isWorking = await testVideoSource(source);
      if (isWorking) {
        source.isWorking = true;
        return [source];
      }
      source.isWorking = false;
    }

    return sources; // Return all sources even if none tested working
  },

  // Get TV show sources with fallback priority
  async getTVSources(tmdbId: number, season: number, episode: number): Promise<VideoSource[]> {
    const configuredApis = getConfiguredApis('tv');
    
    if (configuredApis.length === 0) {
      console.warn('No TV APIs configured');
      return [];
    }

    const sources = configuredApis.map(api => 
      createVideoSource(processVideoUrl(api, 'tv', tmdbId, season, episode), api, 'tv', tmdbId)
    );

    // Test first source and return if working
    for (const source of sources) {
      const isWorking = await testVideoSource(source);
      if (isWorking) {
        source.isWorking = true;
        return [source];
      }
      source.isWorking = false;
    }

    return sources;
  },

  // Get ALL sources for manual switching
  async getAllMovieSources(tmdbId: number): Promise<VideoSource[]> {
    const configuredApis = getConfiguredApis('movie');
    
    return configuredApis.map(api => 
      createVideoSource(processVideoUrl(api, 'movie', tmdbId), api, 'movie', tmdbId)
    );
  },

  async getAllTVSources(tmdbId: number, season: number, episode: number): Promise<VideoSource[]> {
    const configuredApis = getConfiguredApis('tv');
    
    return configuredApis.map(api => 
      createVideoSource(processVideoUrl(api, 'tv', tmdbId, season, episode), api, 'tv', tmdbId)
    );
  },

  // Batch test all sources
  async testAllSources(sources: VideoSource[]): Promise<VideoSource[]> {
    const testPromises = sources.map(async (source) => {
      const isWorking = await testVideoSource(source);
      return { ...source, isWorking };
    });
    
    return Promise.all(testPromises);
  },

  // Get working sources only
  async getWorkingSources(mediaType: 'movie' | 'tv', tmdbId: number, season?: number, episode?: number): Promise<VideoSource[]> {
    const allSources = mediaType === 'movie' 
      ? await this.getAllMovieSources(tmdbId)
      : await this.getAllTVSources(tmdbId, season!, episode!);
    
    const testedSources = await this.testAllSources(allSources);
    return testedSources.filter(source => source.isWorking);
  },

  // Configuration and status methods
  getConfigStatus() {
    return {
      configured: getConfiguredApis().length,
      total: VIDEO_APIS.length,
      apis: VIDEO_APIS.map(api => ({
        name: api.name,
        configured: !!api.baseUrl,
        priority: api.priority,
        supportsTv: api.supportsTv,
        supportsMovies: api.supportsMovies
      }))
    };
  },

  hasConfiguredApis(): boolean {
    return getConfiguredApis().length > 0;
  },

  // Direct URL building for backward compatibility
  buildUrl(apiIndex: number, mediaType: 'movie' | 'tv', tmdbId: number, season?: number, episode?: number): string {
    const api = VIDEO_APIS[apiIndex];
    if (!api || !api.baseUrl) return '';
    
    return processVideoUrl(api, mediaType, tmdbId, season, episode);
  }
};

// Export utilities
export { getConfiguredApis, createVideoSource, processVideoUrl };
