'use client';

import { useState, useEffect } from 'react';
import { useParams, useRouter } from 'next/navigation';
import { tmdbApi } from '@/lib/api/tmdb';
import VideoPlayer from '@/components/player/VideoPlayer';
import { Movie } from '@/lib/api/types';

export default function WatchPage() {
  const params = useParams();
  const router = useRouter();
  const [media, setMedia] = useState<Movie | null>(null);
  const [loading, setLoading] = useState(true);

  const mediaType = params?.mediaType as 'movie' | 'tv';
  const mediaId = parseInt(params?.id as string);

  useEffect(() => {
    if (mediaId && mediaType) {
      loadMediaDetails();
    }
  }, [mediaId, mediaType]);

  const loadMediaDetails = async () => {
    try {
      let data;
      if (mediaType === 'movie') {
        data = await tmdbApi.getMovieDetails(mediaId);
      } else {
        data = await tmdbApi.getTVShowDetails(mediaId);
      }
      setMedia(data);
    } catch (error) {
      console.error('Failed to load media details:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleClose = () => {
    router.back();
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-black flex items-center justify-center">
        <div className="animate-spin rounded-full h-16 w-16 border-b-2 border-white"></div>
      </div>
    );
  }

  if (!media) {
    return (
      <div className="min-h-screen bg-black flex items-center justify-center text-white">
        <div className="text-center">
          <h1 className="text-2xl font-bold mb-4">Media not found</h1>
          <button
            onClick={() => router.back()}
            className="bg-blue-600 hover:bg-blue-700 px-4 py-2 rounded"
          >
            Go Back
          </button>
        </div>
      </div>
    );
  }

  const title = mediaType === 'movie' ? media.title : media.name;

  return (
    <VideoPlayer
      mediaId={mediaId}
      mediaType={mediaType}
      title={title || 'Unknown Title'}
      onClose={handleClose}
    />
  );
}
