'use client';

import { useState, useEffect, useRef } from 'react';
import { X, RotateCcw, Settings } from 'lucide-react';
import { videoApi } from '@/lib/api/video-api';
import { VideoSource } from '@/lib/api/types';

interface VideoPlayerProps {
  mediaId: number;
  mediaType: 'movie' | 'tv';
  title: string;
  season?: number;
  episode?: number;
  onClose: () => void;
}

export default function VideoPlayer({
  mediaId, mediaType, title, season, episode, onClose
}: VideoPlayerProps) {
  const [videoSources, setVideoSources] = useState<VideoSource[]>([]);
  const [currentSourceIndex, setCurrentSourceIndex] = useState(0);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [showSourceSelector, setShowSourceSelector] = useState(false);
  const [retryCount, setRetryCount] = useState(0);
  const iframeRef = useRef<HTMLIFrameElement>(null);

  // Load video sources on component mount
  useEffect(() => {
    loadVideoSources();
  }, [mediaId, mediaType, season, episode]);

  // Handle source changes
  useEffect(() => {
    if (videoSources.length > 0 && currentSourceIndex < videoSources.length) {
      setError(null);
      setLoading(false);
    }
  }, [videoSources, currentSourceIndex]);

  const loadVideoSources = async () => {
    setLoading(true);
    setError(null);
    setRetryCount(0);
    
    try {
      let sources: VideoSource[] = [];
      
      if (mediaType === 'movie') {
        sources = await videoApi.getAllMovieSources(mediaId);
      } else if (mediaType === 'tv' && season !== undefined && episode !== undefined) {
        sources = await videoApi.getAllTVSources(mediaId, season, episode);
      }

      if (sources.length === 0) {
        throw new Error('No video sources configured');
      }

      setVideoSources(sources);
      setCurrentSourceIndex(0);
      
    } catch (err) {
      console.error('Failed to load video sources:', err);
      setError('Failed to load video sources. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleIframeError = () => {
    console.error(`Source ${videoSources[currentSourceIndex]?.servers} failed to load`);
    
    if (currentSourceIndex < videoSources.length - 1) {
      setCurrentSourceIndex(prev => prev + 1);
    } else if (retryCount < 2) {
      setRetryCount(prev => prev + 1);
      setCurrentSourceIndex(0);
    } else {
      setError('All video sources failed to load.');
    }
  };

  const switchSource = (index: number) => {
    if (index >= 0 && index < videoSources.length) {
      setCurrentSourceIndex(index);
      setError(null);
    }
  };

  const retryCurrentSource = () => {
    if (iframeRef.current) {
      iframeRef.current.src = iframeRef.current.src; // Force reload
    }
  };

  const getCurrentSource = () => {
    return videoSources[currentSourceIndex] || null;
  };

  const currentSource = getCurrentSource();
  const videoUrl = currentSource?.url;

  return (
    <div className="fixed inset-0 z-50 bg-black flex items-center justify-center">
      {/* Close Button */}
      <button
        onClick={onClose}
        className="absolute top-4 right-4 z-20 bg-black/50 hover:bg-black/70 text-white p-2 rounded-full transition-colors"
        aria-label="Close player"
      >
        <X className="w-6 h-6" />
      </button>

      {/* Settings Button */}
      {videoSources.length > 1 && (
        <button
          onClick={() => setShowSourceSelector(!showSourceSelector)}
          className="absolute top-4 right-16 z-20 bg-black/50 hover:bg-black/70 text-white p-2 rounded-full transition-colors"
          aria-label="Video settings"
        >
          <Settings className="w-6 h-6" />
        </button>
      )}

      {/* Title and Source Info */}
      <div className="absolute top-4 left-4 z-20 max-w-lg">
        <h2 className="text-white text-xl font-bold mb-1">{title}</h2>
        {mediaType === 'tv' && season && episode && (
          <p className="text-gray-400 text-sm mb-1">
            Season {season} • Episode {episode}
          </p>
        )}
        {currentSource && (
          <div className="text-gray-400 text-sm">
            <p>Source: {currentSource.servers}</p>
            {videoSources.length > 1 && (
              <p>Source {currentSourceIndex + 1} of {videoSources.length}</p>
            )}
          </div>
        )}
      </div>

      {/* Source Selector Dropdown */}
      {showSourceSelector && videoSources.length > 1 && (
        <div className="absolute top-16 right-4 z-20 bg-black/90 backdrop-blur-sm rounded-lg p-4 min-w-48">
          <h3 className="text-white text-sm font-semibold mb-3">Video Sources</h3>
          <div className="space-y-2">
            {videoSources.map((source, index) => (
              <button
                key={index}
                onClick={() => {
                  switchSource(index);
                  setShowSourceSelector(false);
                }}
                className={`w-full text-left px-3 py-2 rounded text-sm transition-colors ${
                  index === currentSourceIndex
                    ? 'bg-blue-600 text-white'
                    : 'bg-gray-800 text-gray-300 hover:bg-gray-700'
                }`}
              >
                <div className="font-medium">{source.servers}</div>
                <div className="text-xs opacity-75">{source.quality}</div>
              </button>
            ))}
          </div>
        </div>
      )}

      {/* Loading State */}
      {loading && (
        <div className="flex flex-col items-center gap-4 text-white">
          <div className="animate-spin rounded-full h-16 w-16 border-b-2 border-white"></div>
          <p>Loading video sources...</p>
        </div>
      )}

      {/* Error State */}
      {error && !loading && (
        <div className="flex flex-col items-center gap-4 text-center max-w-md text-white">
          <div className="text-red-500 text-6xl">⚠️</div>
          <h3 className="text-xl font-bold">Video Unavailable</h3>
          <p className="text-gray-400">{error}</p>
          <div className="flex gap-3">
            {currentSourceIndex < videoSources.length - 1 && (
              <button
                onClick={() => switchSource(currentSourceIndex + 1)}
                className="flex items-center gap-2 bg-blue-600 hover:bg-blue-700 px-4 py-2 rounded-full transition-colors"
              >
                <RotateCcw className="w-4 h-4" />
                Try Next Source
              </button>
            )}
            <button
              onClick={loadVideoSources}
              className="flex items-center gap-2 bg-gray-600 hover:bg-gray-700 px-4 py-2 rounded-full transition-colors"
            >
              <RotateCcw className="w-4 h-4" />
              Reload
            </button>
          </div>
        </div>
      )}

      {/* Video Player */}
      {videoUrl && !loading && !error && (
        <div className="relative w-full h-full">
          <iframe
            ref={iframeRef}
            src={videoUrl}
            title={`Watch ${title}`}
            className="w-full h-full border-0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share"
            allowFullScreen
            onError={handleIframeError}
          />
        </div>
      )}

      {/* API Configuration Warning */}
      {!videoApi.hasConfiguredApis() && !loading && (
        <div className="absolute bottom-4 left-4 right-4 bg-yellow-900/80 text-yellow-100 p-4 rounded-lg">
          <p className="text-sm">
            <strong>Video APIs Not Configured:</strong> Please add your video API URLs to environment variables.
          </p>
        </div>
      )}
    </div>
  );
}
